module.exports = "## ColorPicker\r\n\r\n基于 [pickr](https://github.com/Simonwep/pickr) 改造得颜色选择器。\r\n\r\n## 使用\r\n\r\n```html\r\n<m-color-picker \r\n  id='picker' \r\n  save=\"0\" \r\n  preview=\"0\" \r\n  button='0' \r\n  clear='0' \r\n  width=\"300px\">\r\n</m-color-picker>\r\n\r\n<script>\r\n  var picker = document.querySelector('#picker')\r\n  picker.addEventListener('change', function(evt){\r\n    console.log(evt.detail.color)\r\n  })\r\n</script>\r\n```\r\n\r\n## API\r\n\r\n### Props\r\n\r\n```jsx\r\n{\r\n  button?: boolean;\r\n  preview?: boolean;\r\n  opacity?: boolean;\r\n  hue?: boolean;\r\n  hex?: boolean;\r\n  rgba?: boolean;\r\n  hsla?: boolean;\r\n  hsva?: boolean;\r\n  input?: boolean;\r\n  clear?: boolean;\r\n  save?: boolean;\r\n  cmyk?: boolean;\r\n  default?: string;\r\n  swatches?: string[];\r\n  inline?: boolean;\r\n  strings?: {\r\n      save: string;\r\n      clear: string;\r\n  };\r\n  width?: string;\r\n}\r\n```\r\n\r\n### defaultProps\r\n\r\n```jsx\r\n{\r\n  button: true,\r\n  preview: true,\r\n  opacity: true,\r\n  hue: true,\r\n\r\n  hex: true,\r\n  rgba: true,\r\n  hsla: true,\r\n  hsva: false,\r\n  input: true,\r\n  clear: true,\r\n  save: true,\r\n  cmyk: false,\r\n\r\n  default: '#3365B7',\r\n\r\n  swatches: [],\r\n  inline: true,\r\n\r\n  strings: {\r\n    save: 'Save',  // 保存按钮的文字\r\n    clear: 'Clear' // 清楚按钮的文字\r\n  }\r\n}\r\n```"

